.mobile-blocker {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #24292e;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.mobile-blocker-content {
  text-align: center;
  color: #ffffff;
  padding: 2rem;
}

.mobile-blocker-content i {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.mobile-blocker-content h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.mobile-blocker-content p {
  font-size: 1.1rem;
  margin: 0.5rem 0;
  opacity: 0.8;
} 